import React from "react";
import { useContext } from "react";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/editGame.css";
import QuestionOpen from "./questionOpen";
import QuestionClosed from "./questionClosed";
import AddQuestion from "./addQuestion";

class EditGameClass extends React.Component {
  constructor(props) {
    super(props);
    this.goBack = this.goBack.bind(this);
    this.getUser = this.getUser.bind(this);
    this.fetchGame.bind(this);
    this.fetchTeam.bind(this);
    this.onChangeQuestion = this.onChangeQuestion.bind(this);
    this.onChangeAnswer1 = this.onChangeAnswer1.bind(this);
    this.onChangeAnswer2 = this.onChangeAnswer2.bind(this);
    this.onChangeAnswer3 = this.onChangeAnswer3.bind(this);
    this.onChangeAnswer4 = this.onChangeAnswer4.bind(this);
    this.submitRegister = this.submitRegister.bind(this);
    this.onChangeCorrectAnswer = this.onChangeCorrectAnswer.bind(this);
    this.handleSelected1 = this.handleSelected1.bind(this);
    this.handleSelected2 = this.handleSelected2.bind(this);
    this.handleSelected3 = this.handleSelected3.bind(this);
    this.handleSelected4 = this.handleSelected4.bind(this);
    this.removeQuestion = this.removeQuestion.bind(this);
    this.addQuestion = this.addQuestion.bind(this);
    this.cancel = this.cancel.bind(this);

    this.canceler = null;
    this.state = {
      parentGame: this.props.location,
      parentTeam: "",
      parentClient: "",
      userData: null,
      questions: [],
      question: "",
      answer1: "",
      answer2: "",
      answer3: "",
      answer4: "",
      correctAnswer: "",
      name: "",
      questionId: "",
      parentGameName: "",
      active: false,
      addQuestion: false,
      header: "",
      activeColor: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount = () => {
    this.mounted = true;
    this.fetchGame();
    this.fetchTeam();
    this.getQuestions();
    this.getUser();
    this.canceler = this.props.dbQuestion
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getQuestions();
      });
    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGame();
        this.fetchTeam();
      });
    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchTeam();
        this.fetchGame();
      });
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchTeam();
        this.fetchGame();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  };

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  fetchTeam = () => {
    this.setState({
      parentTeam: null
    });
    this.props.dbGame
      .find({
        selector: {
          _id: this.state.parentGame
        }
      })
      .then(res => {
        const rows = res.docs[0].parent;
        this.props.dbTeam
          .find({
            selector: {
              _id: rows
            }
          })
          .then(res => {
            const data = res.docs;
            this.setState({
              parentTeam: data[0].name
            });
            this.props.dbClient
              .find({
                selector: {
                  _id: rows
                }
              })
              .then(res => {
                const stats = res.docs[0];
                var url = URL.createObjectURL(rows.logo);
                console.log(stats);
                this.setState({
                  parentClient: stats.nameClient,
                  header: stats.header.hex,
                  activeColor: stats.active.hex,
                  secondary: stats.secondary.hex,
                  background: stats.background.hex,
                  headerImage: url
                });
              })
              .catch(err => {
                console.log(err);
              });
          })
          .catch(err => {
            console.log(err);
          });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGame = () => {
    this.setState({
      parentGameName: null
    });
    this.props.dbGame
      .find({
        selector: {
          _id: this.state.parentGame
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          parentGameName: rows[0].name
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getQuestions = () => {
    this.props.dbQuestion
      .find({
        selector: {
          parentGame: this.state.parentGame
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          questions: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };
  onChangeQuestion(e) {
    this.setState({ question: e.target.value });
  }

  onChangeAnswer1(e) {
    this.setState({ answer1: e.target.value });
  }

  onChangeAnswer2(e) {
    this.setState({ answer2: e.target.value });
  }

  onChangeAnswer3(e) {
    this.setState({ answer3: e.target.value });
  }

  onChangeAnswer4(e) {
    this.setState({ answer4: e.target.value });
  }

  removeQuestion(e) {
    console.log(e);
    this.props.dbQuestion
      .find({
        selector: {
          _id: e
        }
      })
      .then(res => {
        console.log(res);
        this.props.dbQuestion.remove(res.docs[0]._id, res.docs[0]._rev);
        window.location.reload(true);
      });
  }

  questionList() {
    return this.state.questions.map((question, i) => (
      <QuestionClosed
        key={i}
        onChangeQuestion={this.onChangeQuestion}
        onChangeAnswer1={this.onChangeAnswer1}
        onChangeAnswer2={this.onChangeAnswer2}
        onChangeAnswer3={this.onChangeAnswer3}
        onChangeAnswer4={this.onChangeAnswer4}
        onChangeCorrectAnswer={this.onChangeCorrectAnswer}
        submitRegister={this.submitRegister}
        cancel={this.cancel}
        parent={this.state.parent}
        questions={this.state.questions}
        question={this.state.questions[i].question}
        answer1={this.state.questions[i].answers[0]}
        answer2={this.state.questions[i].answers[1]}
        answer3={this.state.questions[i].answers[2]}
        answer4={this.state.questions[i].answers[3]}
        correctAnswer={this.state.correctAnswer}
        correct={this.state.questions[i].correct}
        name={this.state.name}
        counter={i}
        handleSelected1={this.handleSelected1}
        handleSelected2={this.handleSelected2}
        handleSelected3={this.handleSelected3}
        handleSelected4={this.handleSelected4}
        questionId={this.state.questions[i]._id}
        removeQuestion={this.removeQuestion}
        _id={this.state.questions[i]._id}
        dbQuestion={this.props.dbQuestion}
        active={this.state.active}
        color={this.state.activeColor}
      />
    ));
  }

  addQuestion = () => {
    this.setState({
      addQuestion: true
    });
  };

  onChangeCorrectAnswer(e) {
    this.setState({ correctAnswer: e.target.value });
  }

  submitRegister(e) {
    e.preventDefault();
    console.log(this.state.parentGame);
    console.log(this.state.question);
    console.log(this.state.answer1);
    this.props.dbQuestion.put({
      _id: new Date().toJSON(),
      parentGame: this.state.parentGame,
      question: this.state.question,
      answers: [
        this.state.answer1,
        this.state.answer2,
        this.state.answer3,
        this.state.answer4
      ],
      correct: this.state.correctAnswer
    });

    this.setState({
      name: "",
      question: "",
      answer1: "",
      answer2: "",
      answer3: "",
      answer4: "",
      correctAnswer: "Answer 1"
    });
  }

  goBack() {
    this.props.history.goBack();
  }

  handleSelected1() {
    this.setState({
      correctAnswer: this.state.answer1
    });
  }

  handleSelected2() {
    this.setState({
      correctAnswer: this.state.answer2
    });
  }

  handleSelected3() {
    this.setState({
      correctAnswer: this.state.answer3
    });
  }

  handleSelected4() {
    this.setState({
      correctAnswer: this.state.answer4
    });
  }

  cancel = () => {
    this.setState({
      active: false
    });
  };

  render() {
    if (this.state.userData) {
      if (this.state.userData.admin || this.state.userData.gameEditor) {
        if (this.state.addQuestion === true) {
          return (
            <AddQuestion
              onChangeQuestion={this.onChangeQuestion}
              onChangeAnswer1={this.onChangeAnswer1}
              onChangeAnswer2={this.onChangeAnswer2}
              onChangeAnswer3={this.onChangeAnswer3}
              onChangeAnswer4={this.onChangeAnswer4}
              onChangeCorrectAnswer={this.onChangeCorrectAnswer}
              submitRegister={this.submitRegister}
              cancel={this.cancel}
              parent={this.state.parent}
              questions={this.state.questions}
              correctAnswer={this.state.correctAnswer}
              name={this.state.name}
              handleSelected1={this.handleSelected1}
              handleSelected2={this.handleSelected2}
              handleSelected3={this.handleSelected3}
              handleSelected4={this.handleSelected4}
              removeQuestion={this.removeQuestion}
              dbQuestion={this.props.dbQuestion}
              color={this.state.activeColor}
            />
          );
        } else {
          return (
            <div className="modal">
              <Header
                admin={this.props.location}
                game={this.props.location}
                background={this.state.header}
                image={this.state.headerImage}
              />
              <button
                className="info back"
                onClick={this.goBack}
                style={{ color: this.state.activeColor }}
              >
                &larr; Back
              </button>
              <div className="info title">
                <h3>
                  {this.state.parentTeam
                    ? this.state.parentTeam
                    : this.state.parentClient}{" "}
                  games
                </h3>
              </div>
              <div className="info add-container">
                <h3>{this.state.parentGameName}</h3>
              </div>
              {this.questionList()}
              <button
                onClick={this.addQuestion}
                style={{ color: this.state.activeColor }}
              >
                add question{" "}
              </button>
            </div>
          );
        }
      }
    } else {
      return null;
    }
  }
}

export default function EditGame(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <EditGameClass
        location={props.location.pathname.replace("/editGame/", "")}
        history={props.history}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
