import React from "react";
import { Link } from "react-router-dom";
import "./stylesheets/App.css";

export default function Header(props) {
  return (
    <div className="header" style={{ background: props.background }}>
      {props.image ? (
        <img src={props.image} alt="client logo" height={40} />
      ) : (
        <img
          src={require("./images/localwisdom.png")}
          alt="Local Wisdom logo"
          height={40}
        />
      )}
      {props.game === null ? null : (
        <Link to={props.admin}>
          <h3>Admin</h3>
        </Link>
      )}
      {props.game === null ? null : (
        <Link to={props.game}>
          <h3>Game</h3>
        </Link>
      )}
      <img src={require("./images/profile.png")} alt="Profile icon" />
    </div>
  );
}
