import React, { Component } from "react";
import "./stylesheets/App.css";

class AddUser extends Component {
  constructor(props) {
    super(props);

    this.state = {
      username: "",
      email: ""
    };
  }

  onClose = e => {
    this.props.onClose &&
      this.props.onClose(e) &&
      this.props.onChangeUsername(this.state.username) &&
      this.props.onChangeEmail(this.state.email);
  };

  onChangeUsername = e => {
    this.setState({
      username: e.target.value
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e.target.value
    });
  };

  handleSubmit = e => {
    this.props.db.put({
      _id: new Date().toJSON(),
      username: this.state.username,
      email: this.state.email,
      parentTeam: this.props.parentTeam
    });
    this.onClose();
  };

  render() {
    console.log(this.state.name);
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="add-user-modal-background">
        <div className="add-user-modal">
          <button onClick={this.onClose}>X</button>
          <h3>Add User</h3>
          <form>
            <label>USERNAME</label>
            <input
              type="text"
              placeholder="user1"
              onChange={this.onChangeUsername}
            />
          </form>
          <div className="hr">
            <div></div>
            <span>OR</span>
            <div></div>
          </div>
          <form>
            <label>EMAIL</label>
            <input
              type="text"
              placeholder="email"
              onChange={this.onChangeEmail}
            />
          </form>
          <button onClick={this.handleSubmit}>Add User</button>
        </div>
      </div>
    );
  }
}

export default AddUser;
