import React from "react";
import GameClosed from "./gameClosed";

class GameOpen extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      active: true,
      doc: "",
      users: null
    };
    this.fetchUsers = this.fetchUsers.bind(this);
    this.getLeaderboard = this.getLeaderboard.bind(this);
    this.getGameById = this.getGameById.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  cancel = () => {
    this.setState({
      active: false
    });
  };

  componentDidMount = () => {
    this.fetchUsers();
    this.getGameById();
    this.canceler = this.props.dbScore
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchUsers();
      });
  };

  fetchUsers = () => {
    this.props.dbScore
      .find({
        selector: {
          gameId: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          users: rows.sort((a, b) =>
            a.score < b.score
              ? 1
              : a.score === b.score
              ? a.user > b.user
                ? 1
                : -1
              : -1
          )
        });
        console.log(this.state.users);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getLeaderboard() {
    if (this.state.users) {
      return this.state.users.slice(0, 5).map((user, i) => {
        return (
          <div className="leaderboard-users" key={i}>
            <h3>{this.state.users[i].user}</h3>
            <h3>{Math.round(this.state.users[i].time / 60)} min</h3>
            <h3>
              {this.state.users[i].correct} / {this.state.users[i].questions}
            </h3>
            <h3>{this.state.users[i].score}</h3>
          </div>
        );
      });
    }
  }

  getGameById = () => {
    this.props.dbGame
      .find({
        selector: {
          _id: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
      })
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    if (this.state.active === false) {
      return (
        <GameClosed
          _id={this.props._id}
          game={this.props.game}
          dbGame={this.props.dbGame}
          dbScore={this.props.dbScore}
          active={this.state.active}
          cancel={this.props.cancel}
          color={this.props.color}
        />
      );
    } else {
      return (
        <div onClick={this.cancel}>
          <div className="card-open" onClick={this.openGame}>
            <div className="card-stats">
              <h3 style={{ color: this.props.color }}>
                {this.props.game.name}
              </h3>
              <h3>Hardcode</h3>
              <h3>15</h3>
              <h3>10 min</h3>
            </div>
          </div>
          <div className="card-leaderboard">{this.getLeaderboard()}</div>
        </div>
      );
    }
  }
}

export default GameOpen;
