import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import UserContext from "./context/userContext";
import GameOpen from "./gameOpen";
import GameClosed from "./gameClosed";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class PlayHomeClass extends Component {
  constructor(props) {
    super(props);
    this.fetchGames.bind(this);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.cancel = this.cancel.bind(this);

    this.state = {
      parentClient: this.props.location,
      userData: null,
      games: null,
      clientName: "",
      active: false,
      header: "",
      activeColor: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchGames();
    this.fetchClient();
    this.getUser();

    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGames();
      });

    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      clientName: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        var url = URL.createObjectURL(rows.logo);
        this.setState({
          clientName: rows.nameClient,
          header: rows.header.hex,
          activeColor: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
          headerImage: url
        });
        console.log(this.state.clientName);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchGames = () => {
    this.setState({
      games: null
    });
    this.props.dbGame
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          games: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  gameList() {
    return this.state.games.map((game, i) => (
      <GameClosed
        key={i}
        _id={game._id}
        game={game}
        dbGame={this.props.dbGame}
        dbScore={this.props.dbScore}
        active={this.state.active}
        cancel={this.cancel}
        color={this.state.activeColor}
      />
    ));
  }

  cancel = () => {
    this.setState({
      active: false
    });
  };

  render() {
    if (this.state.games === null) {
      return null;
    } else if (this.state.userData) {
      return (
        <div>
          <Header
            admin={
              this.state.userData.admin || this.state.userData.teamEditor
                ? "/teams/" + this.state.parentClient
                : this.state.userData.gameEditor
                ? "/games/" + this.state.parentClient
                : this.state.userData.themeEditor
                ? "/themes/" + this.state.parentClient
                : this.state.userData.analyticsViewer
                ? "/editTeams/users/" + this.state.parentClient
                : null
            }
            game={this.props.location}
            background={this.state.header}
            image={this.state.headerImage}
          />
          <div className="info"></div>
          <div className="info title">
            <h3>Hello</h3>
          </div>
          <div className="info add-container">
            <h3>{this.state.userData.firstname}</h3>
          </div>
          <div className="storage-container">
            <div className="card-leaderboard-labels">
              <h3>{this.state.games.length} COMPLETED QUIZZES</h3>
              <h3>ACTUAL COMPLETION TIME</h3>
              <h3>QUESTIONS CORRECT</h3>
              <h3>TOTAL SCORE</h3>
            </div>
            {this.gameList()}
          </div>
        </div>
      );
    } else {
      return null;
    }
  }
}

export default function PlayHome(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <PlayHomeClass
        location={props.location.pathname.replace("/playHome/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
