import Axios from "axios";
//components
import Clients from "./clients";
import Contact from "./contact";
import Teams from "./teams";
import EditTeamGames from "./editTeamGames";
import EditTeamUsers from "./editTeamUsers";
import Users from "./users";
import Games from "./games";
import EditGame from "./editGame";
import Themes from "./themes";
import Play from "./play/play";
import Home from "./components/pages/Home";
import Login from "./components/auth/Login";
import Register from "./components/auth/Register";
import PlayHome from "./playHome";
import UserContext from "./context/userContext";
import CheckOnline from "./checkOnline";
import ProfileAdmin from "./profileAdmin";
import Profile from "./profile";
import ProfileTeam from "./profileTeams";

//stylesheets
import "./stylesheets/App.css";

//packages
import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Route } from "react-router-dom";
import PouchDB from "pouchdb-browser";
import PouchFind from "pouchdb-find";
PouchDB.plugin(PouchFind);

//database creating and sync initiation
const gamesDb = new PouchDB("games_lists");
const gamesRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/games_lists"
);
PouchDB.sync(gamesDb, gamesRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const teamsDb = new PouchDB("teams_lists");
const teamsRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/teams_lists"
);
PouchDB.sync(teamsDb, teamsRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const questionsDb = new PouchDB("questions_lists");
const questionsRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/questions_lists"
);
PouchDB.sync(questionsDb, questionsRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const usersDb = new PouchDB("users_lists");
const usersRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/users_lists"
);
PouchDB.sync(usersDb, usersRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const randomDb = new PouchDB("random"); //currently for analytics and settings (misc stuff)
const randomRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/random"
);
PouchDB.sync(randomDb, randomRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const clientDb = new PouchDB("clients_lists");
const clientRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/clients_lists"
);
PouchDB.sync(clientDb, clientRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const scoreDb = new PouchDB("scores_lists");
const scoreRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/scores_lists"
);
PouchDB.sync(scoreDb, scoreRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});
const ratingDb = new PouchDB("ratings_lists");
const ratingRemoteDatabase = new PouchDB(
  "https://2286caa2-1762-484d-8a76-d92f6bb78e23-bluemix.cloudant.com/ratings_lists"
);
PouchDB.sync(ratingDb, ratingRemoteDatabase, {
  live: true,
  heartbeat: false,
  timeout: false,
  retry: true
});

export default function App() {
  const [userData, setUserData] = useState({
    token: undefined,
    user: undefined
  });

  useEffect(() => {
    const checkLoggedIn = async () => {
      let token = localStorage.getItem("auth-token");
      if (token === null) {
        localStorage.setItem("auth-token", "");
        token = "";
      }
      const tokenRes = await Axios.post(
        "http://localhost:5000/users/tokenIsValid",
        null,
        { headers: { "x-auth-token": token } }
      );
      if (tokenRes.data) {
        const userRes = await Axios.get("http://localhost:5000/users/", {
          headers: { "x-auth-token": token }
        });
        setUserData({
          token,
          user: userRes.data
        });
      }
    };

    checkLoggedIn();
  }, [userData]);

  return (
    <div>
      <CheckOnline />
      <Router>
        <UserContext.Provider value={{ userData, setUserData }}>
          <div className="App">
            <Route exact path="/" component={Home} />
            <Route path="/login" component={Login} />
            <Route
              exact
              path="/register/:id"
              render={props => (
                <Register
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/clients"
              render={props => (
                <Clients
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/profile/:id"
              render={props => (
                <Profile
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/profileTeam/:id"
              render={props => (
                <ProfileTeam
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/profileAdmin/:id"
              render={props => (
                <ProfileAdmin
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/contact/:id"
              render={props => (
                <Contact
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/teams/:id"
              render={props => (
                <Teams
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/editTeam/games/:id"
              render={props => (
                <EditTeamGames
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/editTeam/users/:id"
              render={props => (
                <EditTeamUsers
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/users/:id"
              render={props => (
                <Users
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/games/:id"
              render={props => (
                <Games
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/editGame/:id"
              render={props => (
                <EditGame
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              path="/themes/:id"
              render={props => (
                <Themes
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/play/:id"
              render={props => (
                <Play
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
            <Route
              exact
              path="/playHome/:id"
              render={props => (
                <PlayHome
                  {...props}
                  dbClient={clientDb}
                  dbTeam={teamsDb}
                  dbGame={gamesDb}
                  dbQuestion={questionsDb}
                  dbUser={usersDb}
                  dbScore={scoreDb}
                  dbRating={ratingDb}
                />
              )}
            />
          </div>
        </UserContext.Provider>
      </Router>
    </div>
  );
}
