import React, { Component } from "react";
import "./stylesheets/App.css";

class AddGame extends Component {
  constructor(props) {
    super(props);
    this.state = {
      name: ""
    };
  }

  onClose = e => {
    this.props.onClose &&
      this.props.onClose(e) &&
      this.props.onChangeGameName(this.state.name);
  };

  onChangeName = e => {
    this.setState({
      name: e.target.value
    });
  };

  handleSubmit = e => {
    this.props.db.put({
      _id: new Date().toJSON(),
      name: this.state.name,
      parentClient: this.props.parentClient,
      parentTeam: this.props.parentTeam
    });
    this.onClose();
  };

  render() {
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="add-game-modal-background">
        <div className="add-game-modal">
          <button onClick={this.onClose}>X</button>
          <h3>Create Game</h3>
          <form>
            <label>Game Name</label>
            <input
              type="text"
              placeholder="Game 1"
              onChange={this.onChangeName}
            />
          </form>

          <button onClick={this.handleSubmit}>Create Game</button>
        </div>
      </div>
    );
  }
}

export default AddGame;
