import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./header";


const Team = props => (
  <Link to={"/editTeam/games/" + props.team._id}>
    <div className="card">
      <div className="content">
        <span className="card-name">{props.team.name}</span>
        <span className="team-games">25</span>
        <span className="team-users">10</span>
      </div>
    </div>
  </Link>
);

class ProfileTeam extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user_id: props.location.pathname.replace("/profileTeam/", ""),
      team_id: null,
      teams: null
    }
    this.getUserTeam = this.getUserTeam.bind(this);
    this.getTeams = this.getTeams.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.getUserTeam();
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUserTeam();
      });
      this.canceler = this.props.dbTeam
        .changes({
          since: "now",
          live: true,
          include_docs: true
        })
        .on("change", () => {
          this.getTeams();
        });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  getUserTeam(){
    this.props.dbUser.find({
      selector: {
        _id: this.state.user_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        team_id: rows[0].parentTeam
      });
      console.log(this.state.team_id)
    }).catch(err => {
      console.log(err);
    });
    setTimeout(function() { //Start the timer
    this.getTeams(); //After 1 second, set render to true
}.bind(this), 1000)

  }

  getTeams() {

    this.props.dbTeam.find({
      selector: {
        _id: this.state.team_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        teams: rows
      });
      console.log(this.state.teams);
    }).catch(err => {
      console.log(err);
    });
  }

  teamList() {
    return this.state.teams.map((team, i) => <Team team={team} key={i} />);
  }

  render() {

    if (this.state.teams === null) {
      return null;
    }
    else{
    return (
      <div>
      <Header
        admin={this.props.location}
        game={"/playHome/" + this.state.team_id}
      />

      <Link to={"/profileTeams/" + this.state.user_id}>
        <h3>Teams</h3>
      </Link>
      <Link to={"/profileQuizzes/" + this.state.user_id}>
        <h3>Quizzes</h3>
      </Link>
      {this.teamList()}
      </div>
    )
  }
}

}

export default ProfileTeam;
