import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import AddGame from "./addGame";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

const Game = props => (
  <Link to={"/editGame/" + props.game._id}>
    <div className="card">
      <div className="content">
        <span className="card-name" style={{ color: props.color }}>
          {props.game.name}
        </span>
        <span className="game-questions">15</span>
        <span className="game-time">10 min</span>
      </div>
    </div>
  </Link>
);

class EditTeamGamesClass extends Component {
  constructor(props) {
    super(props);
    this.openModal.bind(this);
    this.fetchGames.bind(this);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);

    this.state = {
      parentTeam: this.props.location,
      parentTeamName: "",
      userData: null,
      games: null,
      parentClientId: "",
      parentClientName: "",
      showModal: false,
      gameName: "",
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchGames();
    this.fetchClient();
    this.getUser();

    this.canceler = this.props.dbGame
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchGames();
      });
    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchGames = () => {
    this.setState({
      games: null
    });
    this.props.dbGame
      .find({
        selector: {
          parentTeam: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        console.log(rows);
        this.setState({
          games: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchClient = () => {
    this.setState({
      parentClientId: ""
    });
    this.props.dbTeam
      .find({
        selector: {
          _id: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          parentClientId: rows.parentClient,
          parentTeamName: rows.name
        });
        this.props.dbClient
          .find({
            selector: {
              _id: rows.parent
            }
          })
          .then(response => {
            const data = response.docs[0];
            var url = URL.createObjectURL(data.logo);
            this.setState({
              parentClientName: data.nameClient,
              header: data.header.hex,
              active: data.active.hex,
              secondary: data.secondary.hex,
              background: data.background.hex,
              headerImage: url
            });
          })
          .catch(err => {
            console.log(err);
          });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  openModal(e) {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeGameName = e => {
    this.setState({
      gameName: e
    });
  };

  gameList() {
    return this.state.games.map((game, i) => (
      <Game game={game} key={i} color={this.state.active} />
    ));
  }

  render() {
    if (!this.state.parentTeamName) {
      return null;
    } else if (this.state.userData) {
      if (
        this.state.userData.admin ||
        this.state.userData.teamEditor ||
        this.state.userData.gameEditor
      ) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClientId}
              background={this.state.header}
              image={this.state.headerImage}
            />
            <div className="info">
              {this.state.userData.admin ||
              this.state.userData.teamEditor ||
              this.state.userData.gameEditor ? (
                <Link
                  className="back"
                  to={"/teams/" + this.state.parentClientId}
                  style={{ color: this.state.active }}
                >
                  <h3 style={{ color: this.state.active }}>
                    &larr; Back to Teams
                  </h3>
                </Link>
              ) : null}
            </div>
            <div className="info title">
              {this.state.userData.admin ||
              this.state.userData.teamEditor ||
              this.state.userData.gameEditor ? (
                <h3>{this.state.parentClientName}</h3>
              ) : null}
            </div>
            <div className="info add-container">
              <h3>{this.state.parentTeamName}</h3>
              {this.state.userData.admin ||
              this.state.userData.teamEditor ||
              this.state.userData.gameEditor ? (
                <form>
                  <input
                    className="add-input"
                    type="submit"
                    value="Add Game +"
                    onClick={this.openModal.bind(this)}
                    style={{ color: this.state.active }}
                  />
                </form>
              ) : null}
              <AddGame
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeGameName={this.onChangeGameName}
                parentTeam={this.state.parentTeam}
                parentClient={this.state.parentClientId}
                db={this.props.dbGame}
              />
            </div>
            <ul className="nav">
              <h3 className="links" style={{ color: this.state.active }}>
                Games
              </h3>
              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <Link
                  className="links"
                  to={"/editTeam/users/" + this.state.parentTeam}
                >
                  Users
                </Link>
              ) : null}
            </ul>

            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.games.length} Active Games</h5>
                <h5 className="questions-label">Questions</h5>
                <h5 className="time-label">Completion Time</h5>
              </div>
              {this.gameList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function EditTeamGames(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <EditTeamGamesClass
        location={props.location.pathname.replace("/editTeam/games/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
