import React, { useState, useContext } from "react";
import { useHistory } from "react-router-dom";
import UserContext from "../../context/userContext";
import Axios from "axios";
import ErrorNotice from "../misc/ErrorNotice";
import "../../stylesheets/App.css";
import "../../stylesheets/Form.scss";

export default function Login() {
  const [email, setEmail] = useState();
  const [password, setPassword] = useState();
  const [error, setError] = useState();

  const { setUserData } = useContext(UserContext);
  const history = useHistory();

  const register = () => history.push("/register");

  const submit = async e => {
    e.preventDefault();
    try {
      const loginUser = { email, password };
      const loginRes = await Axios.post(
        "https://www.summer-game.herokuapp.com/users/login",
        loginUser
      );
      setUserData({
        token: loginRes.data.token,
        user: loginRes.data.user
      });
      localStorage.setItem("auth-token", loginRes.data.token);
      history.push("/");
    } catch (err) {
      err.response.data.msg && setError(err.response.data.msg);
    }
  };
  return (
    <div className="root-container-login">
      <img
        src={require("../../images/localwisdom.png")}
        alt="Local Wisdom logo"
        className="form-img"
      />
      <div className="box-container-login">
        <div className="inner-container-login">
          <div className="login-header">
            <h1>Log In</h1>
          </div>
          <div className="box">
            {error && (
              <ErrorNotice
                message={error}
                clearError={() => setError(undefined)}
              />
            )}
            <form onSubmit={submit}>
              <div className="input-group">
                <label htmlFor="login-email">Email</label>
                <input
                  id="login-email"
                  className="login-input"
                  type="email"
                  placeholder="abc@gmail.com"
                  onChange={e => setEmail(e.target.value)}
                />
              </div>
              <div className="input-group">
                <label htmlFor="login-password">Password</label>
                <input
                  id="login-password"
                  className="login-input"
                  type="password"
                  placeholder="********"
                  onChange={e => setPassword(e.target.value)}
                />
              </div>
              <input type="submit" value="Sign in" className="login-btn" />
            </form>
            <div className="login-extras">
              <button onClick={null} className="login-register">
                Forgot Password?
              </button>
              <button onClick={register} className="login-register">
                Register
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
