import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import AddTeam from "./addTeam";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

const Team = props => (
  <Link to={"/editTeam/games/" + props.team._id}>
    <div className="card">
      <div className="content">
        <span className="card-name" style={{ color: props.color }}>
          {props.team.name}
        </span>
        <span className="team-games">25</span>
        <span className="team-users">10</span>
      </div>
    </div>
  </Link>
);

class TeamsClass extends Component {
  constructor(props) {
    super(props);
    this.openModal.bind(this);
    this.fetchTeams.bind(this);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);

    this.state = {
      parentClient: this.props.location,
      userData: null,
      teams: null,
      clientName: "",
      showModal: false,
      teamName: "",
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchTeams();
    this.fetchClient();
    this.getUser();
    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchTeams();
      });

    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      clientName: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        var url = URL.createObjectURL(rows.logo);
        this.setState({
          clientName: rows.nameClient,
          header: rows.header.hex,
          active: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
          headerImage: url
        });
        console.log(this.state.clientName);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchTeams = () => {
    this.props.dbTeam
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          teams: rows
        });
        console.log(this.state.teams);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  openModal(e) {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeTeamName = e => {
    this.setState({
      teamName: e
    });
  };

  teamList() {
    return this.state.teams.map((team, i) => (
      <Team team={team} key={i} color={this.state.active} />
    ));
  }

  render() {
    if (this.state.teams === null) {
      return null;
    } else if (this.state.userData) {
      if (
        this.state.userData.admin ||
        this.state.userData.teamEditor ||
        this.state.userData.gameEditor
      ) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClient}
              background={this.state.header}
              image={this.state.headerImage}
            />
            <div className="info">
              {this.state.userData.admin ? (
                <Link
                  className="back"
                  to={"/clients"}
                  style={{ color: this.state.active }}
                >
                  <h3 style={{ color: this.state.active }}>
                    &larr; Back to clients
                  </h3>
                </Link>
              ) : null}
            </div>
            <div className="info title">
              {this.state.userData.admin ? <h3>Clients</h3> : null}
            </div>
            <div className="info add-container">
              <h3>{this.state.clientName}</h3>
              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <form>
                  <input
                    className="add-input"
                    type="submit"
                    value="Add Team +"
                    onClick={this.openModal.bind(this)}
                    style={{ color: this.state.active }}
                  />
                </form>
              ) : null}
              <AddTeam
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeTeamName={this.onChangeTeamName}
                parentClient={this.state.parentClient}
                db={this.props.dbTeam}
                color={this.state.active}
              />
            </div>
            <ul className="nav">
              {this.state.userData.admin ? (
                <Link
                  className="links"
                  to={"/contact/" + this.state.parentClient}
                >
                  Contact Information
                </Link>
              ) : null}

              <h3 className="links" style={{ color: this.state.active }}>
                Teams
              </h3>
              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <Link
                  className="links"
                  to={"/users/" + this.state.parentClient}
                >
                  Users
                </Link>
              ) : null}
              {this.state.userData.admin || this.state.userData.gameEditor ? (
                <Link
                  className="links"
                  to={"/games/" + this.state.parentClient}
                >
                  Games
                </Link>
              ) : null}
              {this.state.userData.admin || this.state.userData.themeEditor ? (
                <Link
                  className="links"
                  to={"/themes/" + this.state.parentClient}
                >
                  Themes
                </Link>
              ) : null}
            </ul>

            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.teams.length} Active Teams</h5>
                <h5 className="games-label">Games</h5>
                <h5 className="users-label">Users</h5>
              </div>
              {this.teamList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Teams(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <TeamsClass
        location={props.location.pathname.replace("/teams/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
