import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";

class ContactClass extends Component {
  constructor(props) {
    super(props);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.editContactInformation.bind(this);
    this.readContactInformation.bind(this);
    this.editAddress.bind(this);
    this.readAddress.bind(this);
    this.editBillingAddress.bind(this);
    this.readBillingAddress.bind(this);
    this.onChangeNameFirst.bind(this);
    this.onChangeNameLast.bind(this);
    this.onChangeEmail.bind(this);
    this.onChangePhone.bind(this);
    this.onChangeTitle.bind(this);
    this.onChangeStreet.bind(this);
    this.onChangeTown.bind(this);
    this.onChangeState.bind(this);
    this.onChangeZip.bind(this);
    this.onChangeBillingStreet.bind(this);
    this.onChangeBillingTown.bind(this);
    this.onChangeBillingState.bind(this);
    this.onChangeBillingZip.bind(this);
    this.saveContactInformation.bind(this);
    this.saveAddress.bind(this);
    this.saveBillingAddress.bind(this);
    this.state = {
      parentClient: this.props.location,
      userData: null,
      client: null,
      nameClient: "",
      nameFirst: "",
      nameLast: "",
      email: "",
      phone: "",
      title: "",
      street: "",
      town: "",
      state: "",
      zip: "",
      billingStreet: "",
      billingTown: "",
      billingState: "",
      billingZip: "",
      contactInformation: "read",
      contactAddress: "read",
      contactBillingAddress: "read",
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchClient();
    this.getUser();
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        var url = URL.createObjectURL(rows.logo);
        this.setState({
          client: rows,
          nameClient: rows.nameClient,
          nameFirst: rows.nameFirst,
          nameLast: rows.nameLast,
          email: rows.email,
          phone: rows.phone,
          title: rows.title,
          street: rows.street,
          town: rows.town,
          state: rows.state,
          zip: rows.zip,
          billingStreet: rows.billingStreet,
          billingTown: rows.billingTown,
          billingState: rows.billingState,
          billingZip: rows.billingZip,
          header: rows.header.hex,
          active: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
          headerImage: url
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  editContactInformation = () => {
    this.setState({
      contactInformation: "edit"
    });
  };

  readContactInformation = () => {
    this.setState({
      contactInformation: "read"
    });
  };

  editAddress = () => {
    this.setState({
      contactAddress: "edit"
    });
  };

  readAddress = () => {
    this.setState({
      contactAddress: "read"
    });
  };

  editBillingAddress = () => {
    this.setState({
      contactBillingAddress: "edit"
    });
  };

  readBillingAddress = () => {
    this.setState({
      contactBillingAddress: "read"
    });
  };

  onChangeNameFirst = e => {
    this.setState({
      nameFirst: e.target.value
    });
  };

  onChangeNameLast = e => {
    this.setState({
      nameLast: e.target.value
    });
  };

  onChangeTitle = e => {
    this.setState({
      title: e.target.value
    });
  };

  onChangePhone = e => {
    this.setState({
      phone: e.target.value
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e.target.value
    });
  };

  onChangeStreet = e => {
    this.setState({
      street: e.target.value
    });
  };

  onChangeTown = e => {
    this.setState({
      town: e.target.value
    });
  };

  onChangeState = e => {
    this.setState({
      state: e.target.value
    });
  };

  onChangeZip = e => {
    this.setState({
      zip: e.target.value
    });
  };

  onChangeBillingStreet = e => {
    this.setState({
      billingStreet: e.target.value
    });
  };

  onChangeBillingTown = e => {
    this.setState({
      billingTown: e.target.value
    });
  };

  onChangeBillingState = e => {
    this.setState({
      billingState: e.target.value
    });
  };

  onChangeBillingZip = e => {
    this.setState({
      billingZip: e.target.value
    });
  };

  saveContactInformation = () => {
    const a = this.state.nameFirst;
    const b = this.state.nameLast;
    const c = this.state.email;
    const d = this.state.phone;
    const e = this.state.title;
    const f = this.props.dbClient;
    const g = this.state.parentClient;
    this.props.dbClient
      .get(g)
      .then(function(doc) {
        doc.nameFirst = a;
        doc.nameLast = b;
        doc.email = c;
        doc.phone = d;
        doc.title = e;
        return f.put(doc);
      })
      .then(function() {
        // fetch data again
        return f.get(g);
      })
      .then(function(doc) {
        console.log(doc);
      });
    this.setState({
      contactInformation: "read"
    });
  };

  saveAddress = () => {
    const a = this.state.street;
    const b = this.state.town;
    const c = this.state.state;
    const d = this.state.zip;
    const e = this.props.dbClient;
    const f = this.state.parentClient;
    this.props.dbClient
      .get(f)
      .then(function(doc) {
        doc.street = a;
        doc.town = b;
        doc.state = c;
        doc.zip = d;
        return e.put(doc);
      })
      .then(function() {
        // fetch data again
        return e.get(f);
      })
      .then(function(doc) {
        console.log(doc);
      });
    this.setState({
      contactAddress: "read"
    });
  };

  saveBillingAddress = () => {
    const a = this.state.billingStreet;
    const b = this.state.billingTown;
    const c = this.state.billingState;
    const d = this.state.billingZip;
    const e = this.props.dbClient;
    const f = this.state.parentClient;
    this.props.dbClient
      .get(f)
      .then(function(doc) {
        doc.billingStreet = a;
        doc.billingTown = b;
        doc.billingState = c;
        doc.billingZip = d;
        return e.put(doc);
      })
      .then(function() {
        // fetch data again
        return e.get(f);
      })
      .then(function(doc) {
        console.log(doc);
      });
    this.setState({
      contactBillingAddress: "read"
    });
  };

  render() {
    if (!this.state.client) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClient}
              background={this.state.header}
              image={this.state.headerImage}
            />
            <Link
              className="info back"
              to={"/clients"}
              style={{ color: this.state.active }}
            >
              <h3 style={{ color: this.state.active }}>
                &larr; Back to clients
              </h3>
            </Link>
            <div className="info title">
              <h3>Clients</h3>
            </div>
            <div className="info add-container">
              <h3>{this.state.client.nameClient}</h3>
            </div>
            <ul className="nav">
              <h3 className="links" style={{ color: this.state.active }}>
                Contact Information
              </h3>
              <Link className="links" to={"/teams/" + this.state.parentClient}>
                Teams
              </Link>
              <Link className="links" to={"/users/" + this.state.parentClient}>
                Users
              </Link>
              <Link className="links" to={"/games/" + this.state.parentClient}>
                Games
              </Link>
              <Link className="links" to={"/themes/" + this.state.parentClient}>
                Themes
              </Link>
            </ul>
            <div className="contact-container">
              <div className="contact-heading">
                <h3>Contact Information</h3>
                {this.state.contactInformation === "read" ? (
                  <span onClick={this.editContactInformation}>&#x270E;</span>
                ) : this.state.contactInformation === "edit" ? (
                  <div>
                    <button
                      className="info-save"
                      onClick={this.saveContactInformation}
                      style={{ background: this.state.active }}
                    >
                      Save
                    </button>
                    <button
                      className="info-cancel"
                      onClick={this.readContactInformation}
                      style={{
                        border: "1px solid" + this.state.active,
                        color: this.state.active
                      }}
                    >
                      Cancel
                    </button>
                  </div>
                ) : null}
              </div>
              <div className="contact-card">
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>First Name</h4>
                  {this.state.contactInformation === "read" ? (
                    <h5>{this.state.client.nameFirst}</h5>
                  ) : this.state.contactInformation === "edit" ? (
                    <input
                      type="text"
                      value={this.state.nameFirst}
                      onChange={this.onChangeNameFirst}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Last Name</h4>
                  {this.state.contactInformation === "read" ? (
                    <h5>{this.state.client.nameLast}</h5>
                  ) : this.state.contactInformation === "edit" ? (
                    <input
                      type="text"
                      value={this.state.nameLast}
                      onChange={this.onChangeNameLast}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Title</h4>
                  {this.state.contactInformation === "read" ? (
                    <h5>{this.state.client.email}</h5>
                  ) : this.state.contactInformation === "edit" ? (
                    <input
                      type="text"
                      value={this.state.email}
                      onChange={this.onChangeEmail}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Phone</h4>
                  {this.state.contactInformation === "read" ? (
                    <h5>{this.state.client.phone}</h5>
                  ) : this.state.contactInformation === "edit" ? (
                    <input
                      type="text"
                      value={this.state.phone}
                      onChange={this.onChangePhone}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Email</h4>
                  {this.state.contactInformation === "read" ? (
                    <h5>{this.state.client.title}</h5>
                  ) : this.state.contactInformation === "edit" ? (
                    <input
                      type="text"
                      value={this.state.title}
                      onChange={this.onChangeTitle}
                    />
                  ) : null}
                </div>
              </div>
            </div>
            <div className="contact-container">
              <div className="contact-heading">
                <h3>Address</h3>
                {this.state.contactAddress === "read" ? (
                  <span onClick={this.editAddress}>&#x270E;</span>
                ) : this.state.contactAddress === "edit" ? (
                  <div>
                    <button
                      className="info-save"
                      onClick={this.saveAddress}
                      style={{ background: this.state.active }}
                    >
                      Save
                    </button>
                    <button
                      className="info-cancel"
                      onClick={this.readAddress}
                      style={{
                        border: "1px solid" + this.state.active,
                        color: this.state.active
                      }}
                    >
                      Cancel
                    </button>
                  </div>
                ) : null}
              </div>
              <div className="contact-card">
                <div className="contact-content-street">
                  <h4 style={{ color: this.state.active }}>Street</h4>
                  {this.state.contactAddress === "read" ? (
                    <h5>{this.state.client.street}</h5>
                  ) : this.state.contactAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.street}
                      onChange={this.onChangeStreet}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Town</h4>
                  {this.state.contactAddress === "read" ? (
                    <h5>{this.state.client.town}</h5>
                  ) : this.state.contactAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.town}
                      onChange={this.onChangeTown}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>State</h4>
                  {this.state.contactAddress === "read" ? (
                    <h5>{this.state.client.state}</h5>
                  ) : this.state.contactAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.state}
                      onChange={this.onChangeState}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Zip</h4>
                  {this.state.contactAddress === "read" ? (
                    <h5>{this.state.client.zip}</h5>
                  ) : this.state.contactAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.zip}
                      onChange={this.onChangeZip}
                    />
                  ) : null}
                </div>
              </div>
            </div>
            <div className="contact-container">
              <div className="contact-heading">
                <h3>Billing Address</h3>
                {this.state.contactBillingAddress === "read" ? (
                  <span onClick={this.editBillingAddress}>&#x270E;</span>
                ) : this.state.contactBillingAddress === "edit" ? (
                  <div>
                    <button
                      className="info-save"
                      onClick={this.saveBillingAddress}
                      style={{ background: this.state.active }}
                    >
                      Save
                    </button>
                    <button
                      className="info-cancel"
                      onClick={this.readBillingAddress}
                      style={{
                        border: "1px solid" + this.state.active,
                        color: this.state.active
                      }}
                    >
                      Cancel
                    </button>
                  </div>
                ) : null}
              </div>
              <div className="contact-card">
                <div className="contact-content-street">
                  <h4 style={{ color: this.state.active }}>Street</h4>
                  {this.state.contactBillingAddress === "read" ? (
                    <h5>{this.state.client.billingStreet}</h5>
                  ) : this.state.contactBillingAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.billingStreet}
                      onChange={this.onChangeBillingStreet}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Town</h4>
                  {this.state.contactBillingAddress === "read" ? (
                    <h5>{this.state.client.billingTown}</h5>
                  ) : this.state.contactBillingAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.billingTown}
                      onChange={this.onChangeBillingTown}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>State</h4>
                  {this.state.contactBillingAddress === "read" ? (
                    <h5>{this.state.client.billingState}</h5>
                  ) : this.state.contactBillingAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.billingState}
                      onChange={this.onChangeBillingState}
                    />
                  ) : null}
                </div>
                <div className="contact-content">
                  <h4 style={{ color: this.state.active }}>Zip</h4>
                  {this.state.contactBillingAddress === "read" ? (
                    <h5>{this.state.client.billingZip}</h5>
                  ) : this.state.contactBillingAddress === "edit" ? (
                    <input
                      type="text"
                      value={this.state.billingZip}
                      onChange={this.onChangeBillingZip}
                    />
                  ) : null}
                </div>
              </div>
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Contact(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <ContactClass
        location={props.location.pathname.replace("/contact/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
