import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import ColorPicker from "./colorPicker";
import Logo from "./logo";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";

class ThemesClass extends Component {
  constructor(props) {
    super(props);
    this.fetchClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.state = {
      parentClient: this.props.location,
      userData: null,
      client: "",
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchClient();
    this.getUser();
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      client: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          client: rows.nameClient,
          header: rows.header.hex,
          active: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
        });
        console.log(this.state.client);
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  render() {
    if (!this.state.client) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin || this.state.userData.themeEditor) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClient}
              background={this.state.header}
            />
            <div className="info">
              {this.state.userData.admin ? (
                <Link
                  className="back"
                  to={"/clients"}
                  style={{ color: this.state.active }}
                >
                  <h3 style={{ color: this.state.active }}>
                    &larr; Back to clients
                  </h3>
                </Link>
              ) : null}
            </div>
            <div className="info title">
              {this.state.userData.admin ? <h3>Clients</h3> : null}
            </div>
            <div className="info add-container">
              <h3>{this.state.client}</h3>
            </div>
            <ul className="nav">
              {this.state.userData.admin ? (
                <Link
                  className="links"
                  to={"/contact/" + this.state.parentClient}
                >
                  Contact Information
                </Link>
              ) : null}
              {this.state.userData.admin ||
              this.state.userData.teamEditor ||
              this.state.userData.gameEditor ? (
                <Link
                  className="links"
                  to={"/teams/" + this.state.parentClient}
                >
                  Teams
                </Link>
              ) : null}
              {this.state.userData.admin || this.state.userData.teamEditor ? (
                <Link
                  className="links"
                  to={"/users/" + this.state.parentClient}
                >
                  Users
                </Link>
              ) : null}
              {this.state.userData.admin || this.state.userData.gameEditor ? (
                <Link
                  className="links"
                  to={"/games/" + this.state.parentClient}
                >
                  Games
                </Link>
              ) : null}
              <h3 className="links" style={{ color: this.state.active }}>
                Themes
              </h3>
            </ul>
            <div className="theme-edit">
              <Logo
                client={this.state.parentClient}
                dbClient={this.props.dbClient}
              />
              <ColorPicker
                client={this.state.parentClient}
                dbClient={this.props.dbClient}
              />
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Themes(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <ThemesClass
        location={props.location.pathname.replace("/themes/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
