import React from "react";

class AddQuestion extends React.Component {
  constructor(props) {
    super(props);
  }

  submitQuestion = () => {
    this.props.dbQuestion.find({});
  };

  render() {
    return (
      <div className="edit-section">
        <div className="west">
          <div className="modal-input">
            <label htmlFor="Question" style={{ color: this.props.color }}>
              Question
            </label>
            <input
              type="text"
              name="question"
              className="edit-question"
              placeholder="Question"
              onChange={this.props.onChangeQuestion}
              defaultValue={this.props.question}
            />
            <h6 style={{ color: this.props.color }}>Answer Choices</h6>
          </div>
          <div className="answer-input-container">
            <div className="radio">
              <input
                type="radio"
                value={this.props.answer1}
                name={this.props.counter}
                selected={this.props.correctAnswer === this.props.answer1}
                onChange={this.props.handleSelected1}
              />
            </div>
            <div className="modal-answer-input">
              <input
                type="text"
                name="answer"
                className="edit-answer"
                placeholder="Answer 1"
                onChange={this.props.onChangeAnswer1}
                defaultValue={this.props.answer1}
              />
            </div>
          </div>
          <div className="answer-input-container">
            <div className="radio">
              <input
                type="radio"
                value={this.props.answer2}
                name={this.props.counter}
                selected={this.props.correctAnswer === this.props.answer2}
                onChange={this.props.handleSelected2}
              />
            </div>
            <div className="modal-answer-input">
              <input
                type="text"
                name="answer"
                className="edit-answer"
                placeholder="Answer 2"
                onChange={this.props.onChangeAnswer2}
                defaultValue={this.props.answer2}
              />
            </div>
          </div>
          <div className="answer-input-container">
            <div className="radio">
              <input
                type="radio"
                value={this.props.answer3}
                name={this.props.counter}
                selected={this.props.correctAnswer === this.props.answer3}
                onChange={this.props.handleSelected3}
              />
            </div>
            <div className="modal-answer-input">
              <input
                type="text"
                name="answer"
                className="edit-answer"
                placeholder="Answer 3"
                onChange={this.props.onChangeAnswer3}
                defaultValue={this.props.answer3}
              />
            </div>
          </div>
          <div className="answer-input-container">
            <div className="radio">
              <input
                type="radio"
                value={this.props.answer4}
                name={this.props.counter}
                selected={this.props.correctAnswer === this.props.answer4}
                onChange={this.props.handleSelected4}
              />
            </div>
            <div className="modal-answer-input">
              <input
                type="text"
                name="answer"
                className="edit-answer"
                placeholder="Answer 4"
                onChange={this.props.onChangeAnswer4}
                defaultValue={this.props.answer4}
              />
            </div>
          </div>
          <div className="buttons">
            <button
              type="button"
              className="modal-btn save"
              onClick={this.props.submitRegister}
              style={{
                background: this.props.color
              }}
            >
              Save
            </button>
            <button
              type="button"
              className="modal-btn cancel"
              onClick={this.props.cancel}
              style={{
                color: this.props.color,
                borderColor: this.props.color
              }}
            >
              Cancel
            </button>
            <button
              type="button"
              className="modal-btn delete"
              onClick={() => this.props.removeQuestion(this.props.questionId)}
            >
              Remove Question
            </button>
          </div>
        </div>

        <div className="east">
          <div className="section">
            <h6 style={{ color: this.props.color }}>Question Type</h6>
            <select>
              <option value="multiple choice">Multiple Choice</option>
            </select>
          </div>
          <div className="section">
            <h6 style={{ color: this.props.color }}>Time Limit</h6>
            <select>
              <option value="10">10 sec</option>
              <option value="15">15 sec</option>
              <option value="20">20 sec</option>
              <option value="30">30 sec</option>
            </select>
          </div>
          <div className="section">
            <h6 style={{ color: this.props.color }}>Correct Answer</h6>
            {/**<select>
        <option value={props.answer1}>A. {props.answer1}</option>
        <option value={props.answer2}>B. {props.answer2}</option>
        <option value={props.answer3}>C. {props.answer3}</option>
        <option value={props.answer4}>D. {props.answer4}</option>
      </select>**/}
            <h5>{this.props.correctAnswer}</h5>
          </div>
        </div>
      </div>
    );
  }
}

export default AddQuestion;
