import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import AddUser from "./addUser";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";
import AnalyticsLeaderboard from "./analyticsLeaderboard";
import { CSVLink, CSVDownload } from "react-csv";

const User = props => (
  <Link to={"/editGame/" + props.user._id}>
    <div className="card">
      <div className="content">
        <span className="card-name" style={{ color: props.color }}>
          {props.user.firstname} {props.user.lastname}
        </span>
        <span className="user-quizzes-leaderboard">33</span>
        <span className="user-avgTime-leaderboard">38 sec</span>
        <span className="user-score-leaderboard">{props.user.score}</span>
        <span className="user-games-leaderboard">130</span>
      </div>
    </div>
  </Link>
);

class EditTeamUsersClass extends Component {
  constructor(props) {
    super(props);
    this.openModal.bind(this);
    this.canceler = null;
    this.fetchUsers.bind(this);
    this.fetchClient.bind(this);
    this.fetchScore.bind(this);
    this.fetchData.bind(this);
    this.getUser = this.getUser.bind(this);

    this.state = {
      parentTeam: this.props.location,
      parentTeamName: "",
      userData: null,
      users: null,
      parentClientId: "",
      parentClientName: "",
      username: "",
      email: "",
      showModal: false,
      usersScore: "",
      export: "",
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchUsers();
    this.fetchClient();
    this.fetchScore();
    this.fetchData();
    this.getUser();

    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchUsers();
        this.getUser();
      });

    this.canceler = this.props.dbTeam
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
    this.canceler = this.props.dbScore
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchUsers = () => {
    this.props.dbUser
      .find({
        selector: {
          parentTeam: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          users: rows
        });
        console.log(this.state.users);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchClient = () => {
    this.setState({
      parentClientId: ""
    });
    this.props.dbTeam
      .find({
        selector: {
          _id: this.state.parentTeam
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          parentClientId: rows.parentClient,
          parentTeamName: rows.name
        });
        this.props.dbClient
          .find({
            selector: {
              _id: rows.parent
            }
          })
          .then(response => {
            const data = response.docs[0];
            var url = URL.createObjectURL(data.logo);
            this.setState({
              parentClientName: data.nameClient,
              header: data.header.hex,
              active: data.active.hex,
              secondary: data.secondary.hex,
              background: data.background.hex,
              headerImage: url
            });
            console.log(this.state.parentClientName);
          })
          .catch(err => {
            console.log(err);
          });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchScore = () => {
    this.props.dbScore
      .find({
        selector: {
          gameId: "2020-07-16T04:35:12.509Z"
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          usersScore: rows.sort((a, b) =>
            a.score < b.score
              ? 1
              : a.score === b.score
              ? a.user > b.user
                ? 1
                : -1
              : -1
          )
        });
        console.log(this.state.usersScore);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchData = () => {
    this.props.dbScore
      .allDocs({
        include_docs: true
      })
      .then(res => {
        const rows = res.rows;
        this.setState({
          export: rows.map(row => row.doc)
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  openModal = e => {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeUsername = e => {
    this.setState({
      username: e
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e
    });
  };

  userList() {
    return this.state.users.map((user, i) => (
      <User user={user} key={i} color={this.state.active} />
    ));
  }

  render() {
    if (!this.state.parentTeamName) {
      return null;
    } else if (this.state.userData) {
      if (
        this.state.userData.gameEditor &&
        !this.state.userData.admin &&
        !this.state.userData.teamEditor
      ) {
        return null;
      } else if (this.state.userData.admin || this.state.userData.teamEditor) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClientId}
              background={this.state.header}
              image={this.state.headerImage}
            />
            <div className="info">
              {this.state.userData.admin ? (
                <Link
                  className="back"
                  to={"/teams/" + this.state.parentClientId}
                  style={{ color: this.state.active }}
                >
                  <h3 style={{ color: this.state.active }}>
                    &larr; Back to Teams
                  </h3>
                </Link>
              ) : null}
            </div>
            <div className="info title">
              {this.state.userData.admin ? (
                <h3>{this.state.parentClientName}</h3>
              ) : null}
              {this.state.userData.analyticsViewer ? (
                <CSVLink data={this.state.export} className="export-button">
                  Export Analytics
                </CSVLink>
              ) : null}
            </div>
            <div className="info add-container">
              <h3>{this.state.parentTeamName}</h3>
              <form>
                <input
                  className="add-input"
                  type="submit"
                  value="Add User +"
                  onClick={this.openModal.bind(this)}
                  style={{ color: this.state.active }}
                />
              </form>
              <AddUser
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeUsername={this.onChangeUsername}
                onChangeEmail={this.onChangeEmail}
                parentTeam={this.state.parentTeam}
                db={this.props.dbUser}
              />
            </div>
            <ul className="nav">
              <Link
                className="links"
                to={"/editTeam/games/" + this.state.parentTeam}
              >
                Games
              </Link>
              <h3 className="links" style={{ color: this.state.active }}>
                Users
              </h3>
            </ul>

            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.users.length} Active Users</h5>
                <h5 className="quizzes-label-leaderboard">Quizzes</h5>
                <h5 className="avgTime-label-leaderboard">
                  Average Time to Answer
                </h5>
                <h5 className="score-label-leaderboard">All Time Score</h5>
                {/**}<h5 className="games-label-leaderboard">Games Played</h5>**/}
              </div>
              {this.userList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function EditTeamUsers(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <EditTeamUsersClass
        location={props.location.pathname.replace("/editTeam/users/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
