import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import CreateUser from "./createUser";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

const User = props => (
  <Link to={"/editGame/" + props.user._id}>
    <div className="card">
      <div className="content">
        <span className="card-name" style={{ color: props.color }}>
          {props.user.firstname} {props.user.lastname}
        </span>
        <span className="user-games">20</span>
        <span className="user-email">{props.user.email}</span>
      </div>
    </div>
  </Link>
);

class UsersClass extends Component {
  constructor(props) {
    super(props);
    this.fetchClient.bind(this);
    this.fetchTeams.bind(this);
    this.fetchUsers.bind(this);
    this.openModal.bind(this);
    this.getUser = this.getUser.bind(this);
    this.state = {
      parentClient: this.props.location,
      userData: null,
      parentTeam: "",
      clientName: "",
      teams: "",
      firstname: "",
      lastname: "",
      email: "",
      admin: false,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false,
      player: false,
      users: null,
      header: "",
      active: "",
      secondary: "",
      background: "",
      headerImage: ""
    };
  }

  componentDidMount() {
    this.mounted = true;
    this.fetchClient();
    this.fetchTeams();
    this.fetchUsers();
    this.getUser();
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchClient();
      });

    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.fetchUsers();
        this.getUser();
      });
  }

  componentDidUpdate() {
    document.body.style.background = this.state.background;
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  fetchClient = () => {
    this.setState({
      clientName: ""
    });
    this.props.dbClient
      .find({
        selector: {
          _id: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs[0];
        var url = URL.createObjectURL(rows.logo);
        this.setState({
          clientName: rows.nameClient,
          header: rows.header.hex,
          active: rows.active.hex,
          secondary: rows.secondary.hex,
          background: rows.background.hex,
          headerImage: url
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchTeams = () => {
    this.props.dbTeam
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          teams: rows
        });
        console.log(this.state.teams);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fetchUsers = () => {
    this.props.dbUser
      .find({
        selector: {
          parentClient: this.state.parentClient
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          users: rows
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  userList() {
    return this.state.users.map((user, i) => (
      <User user={user} key={i} color={this.state.active} />
    ));
  }

  openModal = e => {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  };

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeFirstname = e => {
    this.setState({
      firstname: e
    });
  };

  onChangeLastname = e => {
    this.setState({
      lastname: e
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e
    });
  };

  onChangeAdmin = e => {
    this.setState({
      admin: !this.state.admin,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false,
      player: false
    });
  };

  onChangeEditor = e => {
    this.setState({
      admin: false,
      editor: !this.state.editor,
      analytics: true,
      game: true,
      team: true,
      theme: true,
      player: false
    });
  };

  onChangeAnalytics = e => {
    this.setState({
      analytics: !this.state.analytics,
      admin: false,
      player: false
    });
    if (
      !this.state.analytics &&
      this.state.game &&
      this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeGame = e => {
    this.setState({
      game: !this.state.game,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      !this.state.game &&
      this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeTeam = e => {
    this.setState({
      team: !this.state.team,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      this.state.game &&
      !this.state.team &&
      this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangeTheme = e => {
    this.setState({
      theme: !this.state.theme,
      admin: false,
      player: false
    });
    if (
      this.state.analytics &&
      this.state.game &&
      this.state.team &&
      !this.state.theme
    ) {
      this.setState({
        editor: true
      });
    } else {
      this.setState({
        editor: false
      });
    }
  };

  onChangePlayer = e => {
    this.setState({
      player: !this.state.player,
      admin: false,
      editor: false,
      analytics: false,
      game: false,
      team: false,
      theme: false
    });
  };

  onChangeParentTeam = e => {
    this.setState({
      parentTeam: e.target.value
    });
  };

  render() {
    if (this.state.users === null) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin || this.state.userData.teamEditor) {
        return (
          <div>
            <Header
              admin={this.props.location}
              game={"/playHome/" + this.state.parentClient}
              background={this.state.header}
              image={this.state.headerImage}
            />
            <div className="info">
              <Link
                className="back"
                to={"/clients"}
                style={{ color: this.state.active }}
              >
                <h3 style={{ color: this.state.active }}>
                  &larr; Back to clients
                </h3>
              </Link>
            </div>
            <div className="info title">
              <h3>Clients</h3>
            </div>
            <div className="info add-container">
              <h3>{this.state.clientName}</h3>
              <form>
                <input
                  className="add-input"
                  type="submit"
                  value="Create User +"
                  onClick={this.openModal.bind(this)}
                  style={{ color: this.state.active }}
                />
              </form>
              <CreateUser
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeFirstname={this.onChangeFirstname}
                onChangeLastname={this.onChangeLastname}
                onChangeEmail={this.onChangeEmail}
                onChangeAdmin={this.onChangeAdmin}
                onChangeEditor={this.onChangeEditor}
                onChangeAnalytics={this.onChangeAnalytics}
                onChangeGame={this.onChangeGame}
                onChangeTeam={this.onChangeTeam}
                onChangeTheme={this.onChangeTheme}
                onChangePlayer={this.onChangePlayer}
                onChangeParentTeam={this.onChangeParentTeam}
                parentClient={this.state.parentClient}
                dbUser={this.props.dbUser}
                dbTeam={this.props.dbTeam}
                color={this.state.active}
              />
            </div>
            <ul className="nav">
              <Link
                className="links"
                to={"/contact/" + this.state.parentClient}
              >
                Contact Information
              </Link>
              <Link className="links" to={"/teams/" + this.state.parentClient}>
                Teams
              </Link>
              <h3 className="links" style={{ color: this.state.active }}>
                Users
              </h3>
              <Link className="links" to={"/games/" + this.state.parentClient}>
                Games
              </Link>
              <Link className="links" to={"/themes/" + this.state.parentClient}>
                Themes
              </Link>
            </ul>
            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.users.length} Active Users</h5>
                <h5 className="games-label">Games</h5>
                <h5 className="email-label">Email</h5>
              </div>
              {this.userList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Users(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <UsersClass
        location={props.location.pathname.replace("/users/", "")}
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
