import React from "react";
import GameOpen from "./gameOpen";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

class GameClosed extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      doc: "",
      active: false
    };

    this.getGameById = this.getGameById.bind(this);
    this.openGame = this.openGame.bind(this);
  }

  componentDidMount = () => {
    this.getGameById();
  };

  getGameById = () => {
    this.props.dbGame
      .find({
        selector: {
          _id: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
      })
      .catch(err => {
        console.log(err);
      });
  };

  openGame = () => {
    this.getGameById();
    this.setState({
      active: true
    });
  };

  render() {
    if (this.state.doc === "") {
      return null;
    } else if (this.state.active === true) {
      return (
        <GameOpen
          _id={this.state.doc[0]._id}
          game={this.props.game}
          dbGame={this.props.dbGame}
          dbScore={this.props.dbScore}
          cancel={this.props.cancel}
          color={this.props.color}
        />
      );
    } else {
      return (
        <div className="card" onClick={this.openGame}>
          <div className="card-stats">
            <h3 style={{ color: this.props.color }}>{this.props.game.name}</h3>
            <h3>Hardcode</h3>
            <h3>15</h3>
            <h3>10 min</h3>
          </div>
        </div>
      );
    }
  }
}

export default GameClosed;
