import React from "react";
import QuestionClosed from "./questionClosed";

class QuestionOpen extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      active: true,
      doc: ""
    };
    this.getQuestionById = this.getQuestionById.bind(this);
    this.cancel = this.cancel.bind(this);
  }

  cancel = () => {
    this.setState({
      active: false
    });
  };

  componentDidMount = () => {
    this.getQuestionById();
  };

  getQuestionById = () => {
    this.props.dbQuestion
      .find({
        selector: {
          _id: this.props._id
        }
      })
      .then(res => {
        const rows = res.docs;
        this.setState({
          doc: rows
        });
        console.log(this.state.doc);
      })
      .catch(err => {
        console.log(err);
      });
  };

  render() {
    if (this.state.active === false) {
      return (
        <QuestionClosed
          onChangeQuestion={this.props.onChangeQuestion}
          onChangeAnswer1={this.props.onChangeAnswer1}
          onChangeAnswer2={this.props.onChangeAnswer2}
          onChangeAnswer3={this.props.onChangeAnswer3}
          onChangeAnswer4={this.props.onChangeAnswer4}
          onChangeCorrectAnswer={this.props.onChangeCorrectAnswer}
          submitRegister={this.props.submitRegister}
          cancel={this.props.cancel}
          parent={this.state.parent}
          questions={this.state.doc}
          question={this.state.doc[0].question}
          answer1={this.state.doc[0].answers[0]}
          answer2={this.state.doc[0].answers[1]}
          answer3={this.state.doc[0].answers[2]}
          answer4={this.state.doc[0].answers[3]}
          correctAnswer={this.state.correctAnswer}
          correct={this.state.doc[0].correct}
          name={this.state.name}
          handleSelected1={this.props.handleSelected1}
          handleSelected2={this.props.handleSelected2}
          handleSelected3={this.props.handleSelected3}
          handleSelected4={this.props.handleSelected4}
          questionId={this.state.doc[0]._id}
          removeQuestion={this.props.removeQuestion}
          _id={this.state.doc[0]._id}
          dbQuestion={this.props.dbQuestion}
          color={this.props.color}
        />
      );
    } else {
      return (
        <div className="edit-section">
          <div className="west">
            <div className="modal-input">
              <label htmlFor="Question" style={{ color: this.props.color }}>
                Question
              </label>
              <input
                type="text"
                name="question"
                className="edit-question"
                placeholder="Question"
                onChange={this.props.onChangeQuestion}
                defaultValue={this.props.question}
              />
              <h6 style={{ color: this.props.color }}>Answer Choices</h6>
            </div>
            <div className="answer-input-container">
              <div className="radio">
                <input
                  type="radio"
                  value={this.props.answer1}
                  name={this.props._id}
                  selected={this.props.correctAnswer === this.props.answer1}
                  onChange={this.props.handleSelected1}
                />
              </div>
              <div className="modal-answer-input">
                <input
                  type="text"
                  name="answer"
                  className="edit-answer"
                  placeholder="Answer 1"
                  onChange={this.props.onChangeAnswer1}
                  defaultValue={this.props.answer1}
                />
              </div>
            </div>
            <div className="answer-input-container">
              <div className="radio">
                <input
                  type="radio"
                  value={this.props.answer2}
                  name={this.props._id}
                  selected={this.props.correctAnswer === this.props.answer2}
                  onChange={this.props.handleSelected2}
                />
              </div>
              <div className="modal-answer-input">
                <input
                  type="text"
                  name="answer"
                  className="edit-answer"
                  placeholder="Answer 2"
                  onChange={this.props.onChangeAnswer2}
                  defaultValue={this.props.answer2}
                />
              </div>
            </div>
            <div className="answer-input-container">
              <div className="radio">
                <input
                  type="radio"
                  value={this.props.answer3}
                  name={this.props._id}
                  selected={this.props.correctAnswer === this.props.answer3}
                  onChange={this.props.handleSelected3}
                />
              </div>
              <div className="modal-answer-input">
                <input
                  type="text"
                  name="answer"
                  className="edit-answer"
                  placeholder="Answer 3"
                  onChange={this.props.onChangeAnswer3}
                  defaultValue={this.props.answer3}
                />
              </div>
            </div>
            <div className="answer-input-container">
              <div className="radio">
                <input
                  type="radio"
                  value={this.props.answer4}
                  name={this.props._id}
                  selected={this.props.correctAnswer === this.props.answer4}
                  onChange={this.props.handleSelected4}
                />
              </div>
              <div className="modal-answer-input">
                <input
                  type="text"
                  name="answer"
                  className="edit-answer"
                  placeholder="Answer 4"
                  onChange={this.props.onChangeAnswer4}
                  defaultValue={this.props.answer4}
                />
              </div>
            </div>
            <div className="buttons">
              <button
                type="button"
                className="modal-btn save"
                onClick={this.props.submitRegister}
                style={{ background: this.props.color }}
              >
                Save
              </button>
              <button
                type="button"
                className="modal-btn cancel"
                onClick={this.cancel}
                style={{
                  color: this.props.color,
                  borderColor: this.props.color
                }}
              >
                Cancel
              </button>
              <button
                type="button"
                className="modal-btn delete"
                onClick={() => this.props.removeQuestion(this.props.questionId)}
              >
                Remove Question
              </button>
            </div>
          </div>

          <div className="east">
            <div className="section">
              <h6 style={{ color: this.props.color }}>Question Type</h6>
              <select>
                <option value="multiple choice">Multiple Choice</option>
              </select>
            </div>
            <div className="section">
              <h6 style={{ color: this.props.color }}>Time Limit</h6>
              <select>
                <option value="10">10 sec</option>
                <option value="15">15 sec</option>
                <option value="20">20 sec</option>
                <option value="30">30 sec</option>
              </select>
            </div>
            <div className="section">
              <h6 style={{ color: this.props.color }}>Correct Answer</h6>
              {/**<select>
        <option value={props.answer1}>A. {props.answer1}</option>
        <option value={props.answer2}>B. {props.answer2}</option>
        <option value={props.answer3}>C. {props.answer3}</option>
        <option value={props.answer4}>D. {props.answer4}</option>
      </select>**/}
              <h5>{this.props.correct}</h5>
            </div>
          </div>
        </div>
      );
    }
  }
}

export default QuestionOpen;
