//packages
import React from "react";

class CheckOnline extends React.Component {
  constructor(props) {
    super(props);
    this.state = { online: true };
    this.heartBeat.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.heartBeat();
  }

  //not sure what the heartbeat function does but have it in their since the tutorial had it
  heartBeat() {
    if (!this.mounted) {
      return;
    }
    const fetchInit = {
      method: "GET",
      headers: {
        "Content-Type": "application/json"
      },
      mode: "cors",
      cache: "default"
    };
    const fetchRequest = new Request(`http://127.0.0.1:5984/_up`, fetchInit);

    fetch(fetchRequest)
      .then(result => {
        if (!this.mounted) {
          return;
        }
        if (result.ok && !this.state.online) {
          this.setState({
            online: true
          });
        } else if (!result.ok && this.state.online) {
          this.setState({
            online: false
          });
        }
        setTimeout(this.heartBeat.bind(this), 2000);
      })
      .catch(() => {
        if (!this.mounted) {
          return;
        }
        if (this.state.online) {
          this.setState({
            online: false
          });
        }
        setTimeout(this.heartBeat.bind(this), 2000);
      });
  }

  componentWillUnmount() {
    this.mounted = false;
  }

  render() {
    const { online } = this.state;
    return null;
  }
}
export default CheckOnline;
