import React, { Component } from "react";
import { useContext } from "react";
import { Link } from "react-router-dom";
import AddClient from "./addClient";
import Header from "./header";
import UserContext from "./context/userContext";
import "./stylesheets/App.css";
import "./stylesheets/specifics.css";

const Client = props => (
  <Link to={"/contact/" + props.client._id}>
    <div className="card">
      <div className="content">
        <span className="card-name">{props.client.nameClient}</span>
        <span className="client-teams">10</span>
        <span className="client-email">{props.client.email}</span>
      </div>
    </div>
  </Link>
);

class ClientsClass extends Component {
  constructor(props) {
    super(props);
    this.openModal = this.openModal.bind(this);
    this.getClient = this.getClient.bind(this);
    this.getUser = this.getUser.bind(this);
    this.state = {
      clients: null,
      userData: null,
      showModal: false,
      nameClient: "",
      nameFirst: "",
      nameLast: "",
      email: "",
      phone: "",
      title: "",
      street: "",
      town: "",
      state: "",
      zip: "",
      billingStreet: "",
      billingTown: "",
      billingState: "",
      billingZip: ""
    };
  }

  componentDidMount = () => {
    this.mounted = true;
    this.getClient();
    this.getUser();
    this.canceler = this.props.dbClient
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getClient();
      });

    this.canceler = this.props.dbUser
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getUser();
      });
  };

  componentDidUpdate() {
    document.body.style.background = "#F3F3F3";
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
    this.setState({
      userData: null
    });
  }

  getClient = () => {
    this.setState({
      clients: null
    });
    this.props.dbClient
      .allDocs({
        include_docs: true
      })
      .then(res => {
        const rows = res.rows;

        this.setState({
          clients: rows.map(row => row.doc)
        });
      })
      .catch(err => {
        console.log(err);
      });
  };

  getUser() {
    this.props.dbUser
      .find({
        selector: {
          email: this.props.userData
        }
      })
      .then(res => {
        const rows = res.docs[0];
        this.setState({
          userData: rows
        });
        console.log(this.state.userData);
      })
      .catch(err => {
        console.log(err);
      });
  }

  openModal(e) {
    e.preventDefault();
    this.setState({
      showModal: true
    });
  }

  showModal = e => {
    this.setState({
      showModal: !this.state.showModal
    });
  };

  onClose = e => {
    this.props.onClose && this.props.onClose(e);
  };

  onChangeNameClient = e => {
    this.setState({
      nameClient: e
    });
  };

  onChangeNameFirst = e => {
    this.setState({
      nameFirst: e
    });
  };

  onChangeNameLast = e => {
    this.setState({
      nameLast: e
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e
    });
  };

  onChangePhone = e => {
    this.setState({
      phone: e
    });
  };

  onChangeTitle = e => {
    this.setState({
      title: e
    });
  };

  onChangeStreet = e => {
    this.setState({
      street: e
    });
  };

  onChangeTown = e => {
    this.setState({
      town: e
    });
  };

  onChangeState = e => {
    this.setState({
      state: e
    });
  };

  onChangeZip = e => {
    this.setState({
      zip: e
    });
  };

  onChangeBillingStreet = e => {
    this.setState({
      billingStreet: e
    });
  };

  onChangeBillingTown = e => {
    this.setState({
      billingTown: e
    });
  };

  onChangeBillingState = e => {
    this.setState({
      billingState: e
    });
  };

  onChangeBillingZip = e => {
    this.setState({
      billingZip: e
    });
  };

  clientList() {
    return this.state.clients.map((client, i) => (
      <Client client={client} key={i} />
    ));
  }

  render() {
    if (this.state.clients === null) {
      return null;
    } else if (this.state.userData) {
      if (this.state.userData.admin) {
        return (
          <div>
            <Header admin={"/clients"} game={null} />
            <div className="info add-container add-container-client">
              <h3>Clients</h3>
              <form>
                <input
                  className="add-input"
                  type="submit"
                  value="Add Client +"
                  onClick={this.openModal.bind(this)}
                />
              </form>
              <AddClient
                onClose={this.showModal}
                show={this.state.showModal}
                onChangeNameClient={this.onChangeNameClient}
                onChangeNameFirst={this.onChangeNameFirst}
                onChangeNameLast={this.onChangeNameLast}
                onChangeEmail={this.onChangeEmail}
                onChangePhone={this.onChangePhone}
                onChangeTitle={this.onChangeTitle}
                onChangeStreet={this.onChangeStreet}
                onChangeTown={this.onChangeTown}
                onChangeState={this.onChangeState}
                onChangeZip={this.onChangeZip}
                onChangeBillingStreet={this.onChangeBillingStreet}
                onChangeBillingTown={this.onChangeBillingTown}
                onChangeBillingState={this.onChangeBillingState}
                onChangeBillingZip={this.onChangeBillingZip}
                db={this.props.dbClient}
              />
            </div>
            <ul className="nav">
              <h3 className="links">Active</h3>
              <h4 className="links">Inactive</h4>
            </ul>

            <div className="storage-container">
              <div className="labels">
                <h5>{this.state.clients.length} Active Clients</h5>
                <h5 className="teams-label">Teams</h5>
                <h5 className="email-label">Email</h5>
              </div>
              {this.clientList()}
            </div>
          </div>
        );
      } else {
        return null;
      }
    } else {
      return null;
    }
  }
}

export default function Clients(props) {
  const { userData } = useContext(UserContext);
  if (!userData.user) {
    return null;
  }

  if (userData.user) {
    return (
      <ClientsClass
        userData={userData.user.email}
        dbClient={props.dbClient}
        dbTeam={props.dbTeam}
        dbGame={props.dbGame}
        dbQuestion={props.dbQuestion}
        dbUser={props.dbUser}
        dbScore={props.dbScore}
        dbRating={props.dbRating}
      />
    );
  }
}
