import React, { Component } from "react";
import "./stylesheets/App.css";

class AddTeam extends Component {
  constructor(props) {
    super(props);

    this.state = {
      name: ""
    };
  }

  onClose = e => {
    this.props.onClose &&
      this.props.onClose(e) &&
      this.props.onChangeTeamName(this.state.name);
  };

  onChangeName = e => {
    this.setState({
      name: e.target.value
    });
  };

  handleSubmit = e => {
    this.props.db.put({
      _id: new Date().toJSON(),
      name: this.state.name,
      parentClient: this.props.parentClient
    });
    this.onClose();
  };

  render() {
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="add-game-modal-background">
        <div className="add-game-modal">
          <button onClick={this.onClose}>X</button>
          <h3>Create Team</h3>
          <form>
            <label style={{ color: this.props.color }}>Team Name</label>
            <input
              type="text"
              placeholder="Team 1"
              onChange={this.onChangeName}
            />
          </form>

          <button
            onClick={this.handleSubmit}
            style={{ background: this.props.color }}
          >
            Create Team
          </button>
        </div>
      </div>
    );
  }
}

export default AddTeam;
