import React, { Component } from "react";
import "./stylesheets/App.css";

class AddClient extends Component {
  constructor(props) {
    super(props);

    this.state = {
      nameClient: "",
      nameFirst: "",
      nameLast: "",
      email: "",
      phone: "",
      title: "",
      street: "",
      town: "",
      state: "",
      zip: "",
      billingStreet: "",
      billingTown: "",
      billingState: "",
      billingZip: ""
    };
  }

  onClose = e => {
    this.props.onClose &&
      this.props.onClose(e) &&
      this.props.onChangeNameClient(this.state.nameClient) &&
      this.props.onChangeNameFirst(this.state.nameFirst) &&
      this.props.onChangeNameLast(this.state.nameLast) &&
      this.props.onChangeEmail(this.state.email) &&
      this.props.onChangePhone(this.state.phone) &&
      this.props.onChangeTitle(this.state.title) &&
      this.props.onChangeStreet(this.state.street) &&
      this.props.onChangeTown(this.state.town) &&
      this.props.onChangeState(this.state.state) &&
      this.props.onChangeZip(this.state.zip) &&
      this.props.onChangeBillingStreet(this.state.billingStreet) &&
      this.props.onChangeBillingTown(this.state.billingTown) &&
      this.props.onChangeBillingState(this.state.billingState) &&
      this.props.onChangeBillingZip(this.state.billingZip);
  };

  onChangeNameClient = e => {
    this.setState({
      nameClient: e.target.value
    });
  };

  onChangeNameFirst = e => {
    this.setState({
      nameFirst: e.target.value
    });
  };

  onChangeNameLast = e => {
    this.setState({
      nameLast: e.target.value
    });
  };

  onChangeEmail = e => {
    this.setState({
      email: e.target.value
    });
  };

  onChangePhone = e => {
    this.setState({
      phone: e.target.value
    });
  };

  onChangeTitle = e => {
    this.setState({
      title: e.target.value
    });
  };

  onChangeStreet = e => {
    this.setState({
      street: e.target.value
    });
  };

  onChangeTown = e => {
    this.setState({
      town: e.target.value
    });
  };

  onChangeState = e => {
    this.setState({
      state: e.target.value
    });
  };

  onChangeZip = e => {
    this.setState({
      zip: e.target.value
    });
  };

  onChangeBillingStreet = e => {
    this.setState({
      billingStreet: e.target.value
    });
  };

  onChangeBillingTown = e => {
    this.setState({
      billingTown: e.target.value
    });
  };

  onChangeBillingState = e => {
    this.setState({
      billingState: e.target.value
    });
  };

  onChangeBillingZip = e => {
    this.setState({
      billingZip: e.target.value
    });
  };

  handleSubmit = e => {
    this.props.db.put({
      _id: new Date().toJSON(),
      nameClient: this.state.nameClient,
      nameFirst: this.state.nameFirst,
      nameLast: this.state.nameLast,
      email: this.state.email,
      phone: this.state.phone,
      title: this.state.title,
      street: this.state.street,
      town: this.state.town,
      state: this.state.state,
      zip: this.state.zip,
      billingStreet: this.state.billingStreet,
      billingTown: this.state.billingTown,
      billingState: this.state.billingState,
      billingZip: this.state.billingZip,
      header: "#000",
      active: "#000",
      secondary: "#A15BFA",
      background: "#EEE"
    });
    this.onClose();
  };

  render() {
    if (!this.props.show) {
      return null;
    }
    return (
      <div className="add-client-modal-background">
        <div className="add-client-modal">
          <button onClick={this.onClose}>X</button>
          <h3>Create Client</h3>
          <h5 className="modal-label">Contact Information</h5>
          <div className="form-container-client">
            <form>
              <label>Client Name</label>
              <input
                type="text"
                placeholder="Client 1"
                onChange={this.onChangeNameClient}
              />
            </form>
            <form>
              <label>First Name</label>
              <input
                type="text"
                placeholder="First"
                onChange={this.onChangeNameFirst}
              />
            </form>
            <form>
              <label>Last Name</label>
              <input
                type="text"
                placeholder="Last"
                onChange={this.onChangeNameLast}
              />
            </form>
          </div>
          <div className="form-container-client">
            <form>
              <label>Email</label>
              <input
                type="text"
                placeholder="abc@gmail.com"
                onChange={this.onChangeEmail}
              />
            </form>
            <form>
              <label>Phone</label>
              <input
                type="text"
                placeholder="333-666-9999"
                onChange={this.onChangePhone}
              />
            </form>
            <form>
              <label>Title</label>
              <input
                type="text"
                placeholder="CEO"
                onChange={this.onChangeTitle}
              />
            </form>
          </div>
          <h5 className="modal-label">Address</h5>
          <div className="form-container-client">
            <form>
              <label>Street</label>
              <input
                type="text"
                placeholder="123 abc Street"
                onChange={this.onChangeStreet}
              />
            </form>
            <form>
              <label>Town</label>
              <input
                type="text"
                placeholder="New York"
                onChange={this.onChangeTown}
              />
            </form>
            <form>
              <label>State</label>
              <input
                type="text"
                placeholder="NY"
                onChange={this.onChangeState}
              />
            </form>
            <form>
              <label>Zip</label>
              <input
                type="text"
                placeholder="55555"
                onChange={this.onChangeZip}
              />
            </form>
          </div>
          <h5 className="modal-label">Billing Address</h5>
          <div className="form-container-client">
            <form>
              <label>Street</label>
              <input
                type="text"
                placeholder="123 abc Street"
                onChange={this.onChangeBillingStreet}
              />
            </form>
            <form>
              <label>Town</label>
              <input
                type="text"
                placeholder="New York"
                onChange={this.onChangeBillingTown}
              />
            </form>
            <form>
              <label>State</label>
              <input
                type="text"
                placeholder="NY"
                onChange={this.onChangeBillingState}
              />
            </form>
            <form>
              <label>Zip</label>
              <input
                type="text"
                placeholder="55555"
                onChange={this.onChangeBillingZip}
              />
            </form>
          </div>
          <button onClick={this.handleSubmit}>Create Client</button>
        </div>
      </div>
    );
  }
}

export default AddClient;
