import React, { Component } from "react";
import StarRating from "./play/starRating";
import "./stylesheets/play.css";
import "./stylesheets/finishGame.css";

class AnalyticsLeaderboard extends Component {
  constructor(props) {
    super(props);
    const { correct, total, score, time, scores, users, gameId } = this.props;
    this.goBack = this.goBack.bind(this);
    this.getPercent = this.getPercent.bind(this);
    this.getLeaderboard = this.getLeaderboard.bind(this);
    this.state = {
      gameName: ""
    };
  };

componentDidMount = () => {

}
  goBack() {
    this.props.history.goBack();
  }

  getPercent() {
    return Math.round((this.props.correct / this.props.total) * 100);
  }

  getLeaderboard() {
    if (this.props.users) {
      return this.props.users.slice(0, 5).map((user, i) => {
        return (

          <div className="card" key={i}>
            <span className="card-name">{this.props.users[i].user}</span>
            <h3>{Math.round(this.props.users[i].time / 60)} min</h3>
            <h3>
              {this.props.users[i].correct} / {this.props.users[i].questions}
            </h3>
            <h3>{this.props.users[i].score}</h3>
          </div>

        );
      });
    }
  }

  /**getGameName = () => {
      this.props.dbGame
        .find({
          selector: {
            _id: "2020-07-16T04:35:12.509Z"
          }
        })
        .then(res => {
          const rows = res.docs;
          this.setState({
            gameName: rows[0].name
          });
        })
        .catch(err => {
          console.log(err);
        });
    };**/

  render() {
    return (
      <div className = "storage-container">
        <div className="labels">
          <h5>ACTUAL COMPLETION TIME</h5>
          <h5>QUESTIONS CORRECT</h5>
          <h5>TOTAL SCORE</h5>
        </div>
        <div className="card">{this.getLeaderboard()}</div>
      </div>
    );
  }
}

export default AnalyticsLeaderboard;
