import React, { useState, useEffect } from "react";
import { ChromePicker } from "react-color";
import "./stylesheets/App.css";

export default function ColorPicker(props) {
  const [header, setHeader] = useState("");
  const [active, setActive] = useState("");
  const [secondary, setSecondary] = useState("");
  const [background, setBackground] = useState("");
  const [edit, setEdit] = useState(false);

  useEffect(() => {
    fetchClient();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const fetchClient = () => {
    props.dbClient
      .find({
        selector: {
          _id: props.client
        }
      })
      .then(res => {
        const rows = res.docs[0];
        setHeader(rows.header.hex);
        setActive(rows.active.hex);
        setSecondary(rows.secondary.hex);
        setBackground(rows.background.hex);
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  const saveColors = () => {
    props.dbClient
      .get(props.client)
      .then(function(doc) {
        doc.header = header;
        doc.active = active;
        doc.secondary = secondary;
        doc.background = background;
        return props.dbClient.put(doc);
      })
      .then(function() {
        // fetch data again
        return props.dbClient.get(props.client);
      })
      .then(function(doc) {
        console.log(doc);
      });
    setEdit(edit => !edit);
  };

  return (
    <div>
      <button onClick={() => setEdit(edit => !edit)}>
        {edit ? "close" : "edit"}
      </button>
      {edit && <button onClick={saveColors}>save</button>}

      {edit ? (
        <div className="colors">
          <div>
            <h5>Header color</h5>
            <ChromePicker
              disableAlpha={true}
              color={header}
              onChange={updatedHeader => setHeader(updatedHeader)}
            />
          </div>
          <div>
            <h5>Active color</h5>
            <ChromePicker
              disableAlpha={true}
              color={active}
              onChange={updatedActive => setActive(updatedActive)}
            />
          </div>
          <div>
            <h5>Secondary color</h5>
            <ChromePicker
              disableAlpha={true}
              color={secondary}
              onChange={updatedSecondary => setSecondary(updatedSecondary)}
            />
          </div>
          <div>
            <h5>Background color</h5>
            <ChromePicker
              disableAlpha={true}
              color={background}
              onChange={updatedBackground => setBackground(updatedBackground)}
            />
          </div>
        </div>
      ) : (
        <div className="colorsDisplay">
        <h3>Color Pallete</h3>
          <div className="display-section">
            <div className="circleDisplay" style={{ background: header }}></div>
            <h5>Header</h5>
          </div>
          <div className="display-section">
            <div className="circleDisplay" style={{ background: active }}></div>
            <h5>Active State</h5>
          </div>
          <div className="display-section">
            <div
              className="circleDisplay"
              style={{ background: secondary }}
            ></div>
            <h5>Secondary Color</h5>
          </div>
          <div className="display-section">
            <div
              className="circleDisplay"
              style={{ background: background }}
            ></div>
            <h5>Background</h5>
          </div>
        </div>
      )}
    </div>
  );
}
