import React, { Component } from "react";
import StarRating from "./starRating";
import "../stylesheets/play.css";
import "../stylesheets/finishGame.css";

class FinishGame extends Component {
  constructor(props) {
    super(props);
    const {
      correct,
      total,
      score,
      time,
      scores,
      users,
      dbRating,
      history
    } = this.props;
    this.goBack = this.goBack.bind(this);
    this.getPercent = this.getPercent.bind(this);
    this.getLeaderboard = this.getLeaderboard.bind(this);
  }

  goBack() {
    this.props.history.goBack();
  }

  getPercent() {
    return Math.round((this.props.correct / this.props.total) * 100);
  }

  getLeaderboard() {
    if (this.props.users) {
      return this.props.users.slice(0, 5).map((user, i) => {
        return (
          <div className="leaderboard-users" key={i}>
            <h3>{this.props.users[i].user}</h3>
            <h3>{Math.round(this.props.users[i].time / 60)} min</h3>
            <h3>
              {this.props.users[i].correct} / {this.props.users[i].questions}
            </h3>
            <h3>{this.props.users[i].score}</h3>
          </div>
        );
      });
    }
  }

  render() {
    return (
      <div>
        <div className="result-rating">
          <h4 className="result-label">Your results</h4>
          <div className="rating-container">
            <h5>Rate this quiz!</h5>
            <StarRating
              dbRating={this.props.dbRating}
              history={this.props.history}
              color={this.props.star}
            />
          </div>
        </div>
        <div className="stats">
          <div className="stat">
            <h3>Total Time</h3>
            <h4 style={{ color: this.props.color }}>
              {Math.round(this.props.time / 60)}
              <span style={{ color: this.props.color }}>min</span>
            </h4>
          </div>

          <div className="stat">
            <h3>% Answered Correctly</h3>
            <h4 style={{ color: this.props.color }}>{this.getPercent()}%</h4>
          </div>

          <div className="stat">
            <h3>Total Score</h3>
            <h4 style={{ color: this.props.color }}>{this.props.score}</h4>
          </div>
        </div>
        <div className="leaderboard-labels">
          <h3>Leaderboard</h3>
          <h3>ACTUAL COMPLETION TIME</h3>
          <h3>QUESTIONS CORRECT</h3>
          <h3>TOTAL SCORE</h3>
        </div>
        <div className="leaderboard">{this.getLeaderboard()}</div>
      </div>
    );
  }
}

export default FinishGame;
