import React, { Component } from "react";
import "./stylesheets/App.css";

class Logo extends Component {
  state = {
    selectedFile: null
  };

  componentDidMount() {
    this.fetchClient();
  }

  fetchClient = () => {
    this.props.dbClient
      .find({
        selector: {
          _id: this.props.client
        }
      })
      .then(res => {
        const rows = res.docs[0];
        console.log(rows);
      })
      .catch(err => {
        console.log(err);
      });
  };

  fileSelectedhandler = event => {
    this.setState({
      selectedFile: event.target.files[0]
    });
  };

  fileUploadHandler = () => {
    const c = this.props.dbClient;
    const d = this.props.client;
    // post to couch
    const fd = this.state.selectedFile;
    c.get(d)
      .then(function(doc) {
        doc.logo = fd;
        return c.put(doc);
      })
      .then(function() {
        // fetch data again
        return c.get(d);
      })
      .then(function(doc) {
        console.log(doc);
      });
  };

  render() {
    return (
      <div>
        <input type="file" onChange={this.fileSelectedhandler} />
        <button onClick={this.fileUploadHandler}>Upload</button>
      </div>
    );
  }
}

export default Logo;
