import React, { Component } from "react";
import { Link } from "react-router-dom";
import Header from "./header";
import { Multiselect } from "multiselect-react-dropdown";

class ProfileAdmin extends Component {
  constructor(props) {
    super(props);

    this.state = {
      user_id: props.location.pathname.replace("/profileAdmin/", ""),
      totalScore: null,
      scoreDocs: null,
      gamesPlayed: null,
      avgTimeAnswer: null,
      user: null,
      username: "",
      lastname: "",
      firstname: "",
      email: "",
      admin: null,
      analyticsViewer: null,
      gameEditor: null,
      teamEditor: null,
      themeEditor: null,
      playerOnly: null,
      options: [
        {name: "Admin", id: "admin"},
        {name: "Analytics Viewer", id: "analyticsViewer"},
        {name: "Game Editor", id: "gameEditor"},
        {name: "Team Editor", id: "teamEditor"},
        {name: "Theme Editor", id: "themeEditor"},
        {name: "Player Only", id: "playerOnly"},
      ],
      selectedPermissions: [],
      image: ""
    };
    this.getScoreDocs = this.getScoreDocs.bind(this);
    this.getSelectedPermissions = this.getSelectedPermissions.bind(this);
    this.getUser = this.getUser.bind(this);
    this.onSelect = this.onSelect.bind(this);
  }

  componentDidMount() {
    this.mounted = true;
    this.getScoreDocs();
    this.getUser();
    this.canceler = this.props.dbScore
      .changes({
        since: "now",
        live: true,
        include_docs: true
      })
      .on("change", () => {
        this.getScoreDocs();
      });

      this.canceler = this.props.dbUser
        .changes({
          since: "now",
          live: true,
          include_docs: true
        })
        .on("change", () => {
          this.getUser();
        });
  }

  componentWillUnmount() {
    this.canceler.cancel();
    this.mounted = false;
  }

  getScoreDocs() {
    this.props.dbScore.find({
      selector: {
        user: this.state.user_id
      }
    }).then(res => {
        const rows = res.docs;
            this.setState({
              scoreDocs: rows,
              gamesPlayed: rows.length,

            });
            console.log(this.state.scoreDocs);
          })
          .catch(err => {
            console.log(err);
          });
      }

  getUser() {
    this.props.dbUser.find({
      selector: {
        _id: this.state.user_id
      }
    }).then(res => {
      const rows = res.docs;
      this.setState({
        user: rows,
        username: rows[0].username,
        email: rows[0].email,
        firstname: rows[0].firstname,
        lastname: rows[0].lastname,
        admin: rows[0].admin,
        analyticsViewer: rows[0].analyticsViewer,
        gameEditor: rows[0].gameEditor,
        teamEditor: rows[0].teamEditor,
        themeEditor: rows[0].themeEditor,
        playerOnly: rows[0].playerOnly,
        image: rows[0].image
      });
      console.log(this.state.user);
      this.getSelectedPermissions();
    }).catch(err => {
      console.log(err);
    });
  }

  getSelectedPermissions() {
    if(this.state.admin === true){
      const added = [{name: "Admin", id: "admin"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.analyticsViewer === true){
      const added = [{name: "Analytics Viewer", id: "analyticsViewer"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.gameEditor === true){
      const added = [{name: "Game Editor", id: "gameEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.teamEditor === true){
      const added = [{name: "Team Editor", id: "teamEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.themeEditor === true){
      const added = [{name: "Theme Editor", id: "themeEditor"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
    if(this.state.playerOnly === true){
      const added = [{name: "Player Only", id: "playerOnly"}]
      this.setState(prevState => ({
        selectedPermissions: [...prevState.selectedPermissions, ...added]
      }))
    }
  }

  onSelect(selectedList, selectedItem) {
    const db = this.props.dbUser;
    const user_id = this.state.user_id;
    if(selectedItem.id === "admin"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.admin = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "analyticsViewer"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.analyticsViewer = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "gameEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.gameEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "teamEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.teamEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "themeEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.themeEditor = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(selectedItem.id === "playerOnly"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.playerOnly = true;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }
  }

  onRemove(selectedList, removedItem) {
    const db = this.props.dbUser;
    const user_id = this.state.user_id;
    if(removedItem.id === "admin"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.admin = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "analyticsViewer"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.analyticsViewer = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "gameEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.gameEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "teamEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.teamEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "themeEditor"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.themeEditor = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }

    if(removedItem.id === "playerOnly"){
      this.props.dbUser.get(this.state.user_id).then(function (doc) {
        doc.playerOnly = false;
        return db.put(doc);
        console.log(doc);
      }).then(function () {
        return db.get(user_id);

      }).then(function (doc) {
        console.log(doc);
      })
    }
  }

  render() {

    if (this.state.user === null) {
      return null;
    }
    else{
    return (
      <div>
        <Header
          admin={this.props.location}
          game={"/playHome/" + this.state.user[0].parentClient}
        />
      {/** back button **/}
        <Link className="info back" to={"/playHome/" + this.state.user[0].parentClient}>
          <h3>&larr; Back to Games</h3>
        </Link>

        <Link to={"/profileTeams/" + this.state.user_id}>
          <h3>Teams</h3>
        </Link>
        <Link to={"/profileQuizzes/" + this.state.user_id}>
          <h3>Quizzes</h3>
        </Link>
        {/** profile div **/}
        <div>
          {/** profile pic **/}
          <img alt="profile_pic" src={this.state.image} style={{height: 150, width: 150}}/>
          {/** first name **/}
          <p> Firstname: {this.state.firstname} </p>
          {/** last name **/}
          <p> Lastname: {this.state.lastname} </p>
          {/** email **/}
          <p>Email: {this.state.email} </p>
          {/** username **/}
          <p>Username: {this.state.username} </p>
          {/** user type **/}
          <Multiselect
          options={this.state.options}
          selectedValues={this.state.selectedPermissions}
          onSelect={this.onSelect.bind(this)}
          onRemove={this.onRemove.bind(this)}
          displayValue="name"
          />
        </div>
        {/** score div **/}
        <div>
          {/** all time score **/}
          <p>Total Score: 660 </p>
          {/** number of games played **/}
          <p> Games Played: {this.state.gamesPlayed} </p>
          {/** average time to answer **/}
          <p> Average Time to Answer: 12 sec </p>
          {/** percent answered correctly **/}
          <p> Percentage Answered Correctly: 80% </p>
      </div>
      </div>
    )
  }
  }
}

export default ProfileAdmin;
